body {
  font-family: Roboto, sans-serif; /* Уберем кавычки вокруг 'Roboto' */
  margin: 0;
  padding: 0;
}

header {
  background-color: #fff; /* Изменим цвет на #fff */
  color: white;
  text-align: center;
  width: 100%;
  display: flex;
}


nav {
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  height: 60px;
  box-sizing: border-box;
}

nav a {
  color: white;
  text-decoration: none;
  padding: 23px 22px 20px;
  height: 60px;
  align-items: center;
}

.logo {
  margin-right: 243px;
  padding: 10px 50px;
}

nav a.is-active {
  color: #00ACDC;
}

nav img[src="logo.png"] {
  width: 40px;
  height: 40px;
  flex-shrink: 0;
}

nav a[href="#apple"] {
  color: #00ACDC;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}

nav a[href="#samsung"],
nav a[href="#Smartphones"],
nav a[href="#gadgets"],
nav a[href="#tablets"],
nav a[href="#photo"],
nav a[href="#video"] {
  color: #000;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}

nav a[href="#Laptops & Computers"] {
  color: #00ACDC;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  margin-right: 50px;
}
